.header {
  grid-area: header;
}

.main {
  grid-area: main;
}

.left {
  grid-area: left;
}

.right {
  grid-area: right;
}

.footer {
  grid-area: footer;
}

.header {
  border: 1px solid rgb(230, 229, 229);
  height: 100%;
  width: auto;
  position: relative;
  /*background-image: url(/images/ai/skupinka1.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;*/
}

header ::before {
  content: "";
  display: block;
  position: absolute;
  inset: 0;
  background: url(/images/ai/skupinka_1.png);
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: rgb(230, 229, 229);
  opacity: 0.03;
  z-index: -1;
}

h1 {
  color: rgb(190, 3, 3);
  font-family: "Dancing Script", cursive;
  font-size: 100px;
  margin: 30px;
}

ul {
  display: inline;
}

li {
  display: inline;
  margin: 0;
  font-weight: 600;
  padding: 10px;
}

a {
  text-decoration: none;
  color: rgb(190, 3, 3);
}

a:hover {
  color: red;
  /*rgb(113, 39, 217);*/
}

.main {
  margin-left: 20px;
  margin-right: 20px;
}

